<template>
    <div>
        <div class="j-ta-c j-m-tb-S">
            <van-uploader v-model="fileList" disabled :max-count="1"/>
        </div>
        <div class="j-ta-c j-m-tb-S">
            <van-uploader :after-read="upload">
                <van-button icon="user-o" type="primary">更换头像</van-button>
            </van-uploader>
        </div>
    </div>
</template>

<script>

    export default {
        components: {},
        data() {
            return {
                fileList: [{content: 'https://fuyun01.oss-cn-zhangjiakou.aliyuncs.com/fileUpload/user/userPortrait.jpg'}],
            }
        },
        created() {
        },
        methods: {
            upload(file) {
                console.log(file)
                let formData = new FormData();
                formData.append('file', file.file);
                let headers = {headers: {'Content-Type': 'multipart/form-data'}}
                this.axios.post('/api/upload/avatar', formData, headers)
                    .then((res) => {
                        this.fileList[0].content = res.data.url
                        this.$toast('更换成功');
                        console.log(res);
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            },
        },
    }
</script>
